"use strict";

/* beautify ignore:start */
import {MDCRipple} from '@material/ripple';
/* beautify ignore:end */

/**
 * Build material icon and attach to DOM
 * <button class="mdc-icon-button"
 *  title="..."
 *  data-mdc-ripple-is-unbounded="true"
 *  <svg xmlns="http://www.w3.org/2000/svg" width="24" height="24" viewBox="0 0 24 24">
 *    <path d="M0 0h24v24H0z" fill="none"/>
 *    <path d="{@mdi/js icon}"/>
 *  </svg>
 * </button>
 * @param root The DOM element to append the icon to.
 * @param icon The @mdi/js icon
 */
export class EARMaterialIcon {
  constructor(root, icon) {
    this.container;
    this.icon;
    this.root = root;
    /* Can't do the following because root may contain multiple icos
    Object.defineProperty(this.root, 'EARMaterialIcon', {
      configurable: true,
      enumerable: false,
      value: this,
      writable: false
    });
    */
    this.attach(icon);
  }

  addClass(value) {
    this.container.classList.add(value);
  }

  /**
   * Build icon and attach to DOM
   * @param d The @mdi/js icon (path d attribute)
   */
  attach(d) {
    this.container = document.createElement("a");
    this.container.classList.add("mdc-icon-button");
    this.container.setAttribute("data-mdc-ripple-is-unbounded", true);
    this.container.href = '#';

    const svg = document.createElementNS("http://www.w3.org/2000/svg", "svg");
    const path1 = document.createElementNS("http://www.w3.org/2000/svg", 'path');
    const path2 = document.createElementNS("http://www.w3.org/2000/svg", 'path');

    svg.classList.add("mdc-icon-button__icon");
    svg.setAttribute("aria-hidden", "true");
    svg.setAttribute('viewbox', '0 0 24 24');
    svg.setAttribute('width', '24px');
    svg.setAttribute('height', '24px');

    path1.setAttribute('d', 'M0 0h24v24H0z');
    path1.setAttribute('fill', 'none');

    path2.setAttribute('d', d);
    //path2.setAttribute('fill', '#2962ff');

    svg.appendChild(path1);
    svg.appendChild(path2);
    this.icon = svg;
    this.container.appendChild(this.icon);
    this.root.appendChild(this.container);
    const iconButtonRipple = new MDCRipple(this.container);
    iconButtonRipple.unbounded = true;
  }

  setAttribute(name, value) {
    this.container.setAttribute(name, value);
  }

  setHref(url) {
    this.container.href = url;
    if (url.startsWith("http")) {
      this.setTarget("_blank");
    }
  }

  setTarget(target) {
    this.container.setAttribute("target", target);
  }

  setTitle(title) {
    this.container.setAttribute("title", title);
  }

}
